<template>
  <div class="home">
    <p class="text-4xl text-MyColor-H3 font-bold pt-4">
      論小型自動化自娛娛人歡樂盆栽實行遠種植之開開心心零距離培養計畫
    </p>
    <p class="text-3xl text-MyColor-H5">視訊</p>
    <br />
    <canvas id="va" class=" w-48 h-32"></canvas>
    <button
      class="m-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
      @click="TopDownAngle(5)"
    >
      往上
    </button>
    <button
      @click="TopDownAngle(-5)"
      class="m-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
    >
      往下
    </button>
    <br />
    <button
      @click="LeftRightAngle(5)"
      class="m-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
    >
      往左
    </button>
    <button
      @click="LeftRightAngle(-5)"
      class="m-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
    >
      往右
    </button>

    <br />
  </div>
</template>

<script>
// @ is an alias to /src
export default {
  name: "Video",
  components: {},
  data() {
    return {
      TopDown: { _angle: 90 },
      LeftRight: { _angle: 90 }
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      let _this = this;
      window.boardReady({ device: "aVLvB", multi: true }, function(board) {
        board.samplingInterval = 250;
        console.log(0);
        _this.TopDown = new window.getServo(board, 9);
        _this.TopDown._angle = 90;
        _this.LeftRight = new window.getServo(board, 3);
        _this.LeftRight._angle = 90;
      });
      async () =>
        new window.Camera("http://webduino.tw/view/cam18").onCanvas("va");
    },
    LeftRightAngle(a) {
      if (
        (a >= 0 && this.LeftRight._angle < 175) ||
        (a <= 0 && this.LeftRight._angle > 5)
      ) {
        this.LeftRight._angle += a;
      }
    },
    TopDownAngle(a) {
      if (
        (a >= 0 && this.TopDown._angle < 175) ||
        (a <= 0 && this.TopDown._angle > 5)
      ) {
        this.TopDown._angle += a;
      }
    }
  }
};
</script>
